.RightSide{
    display: flex;
    flex-direction: column;
    width: 95%;
    justify-content: space-evenly;
}
@media screen and (max-width:1200px){
    .RightSide{
        justify-content: flex-start;
        margin-top: 3rem;
    }
}
@media screen and (max-width: 768px) {
    .RightSide{
        width: 100%;
        margin-top: 0;

    }
    .RightSide>div{
        display: flex;
        align-items: center;
        flex-direction: column;
    }
}